import React, { useState, useEffect } from 'react';
import axios from 'axios';
import GoogleMapReact from 'google-map-react';
import { Card, CardDeck, Container, Row, Col, Button, Jumbotron, Collapse} from 'react-bootstrap';
import animateScrollTo from 'animated-scroll-to';

function DataFetching() {
    const[ posts, setposts ] = useState([]);
    const[ values, setvalue ] = useState([]);
    const[ open, setOpen ] = useState(false);
  
    //used axios to fetch data from API link
    useEffect(() => {
          
        axios.get('https://api.openbrewerydb.org/breweries?by_city=harrisburg')
            .then(res => {
            setposts (res.data) 
            })
            .catch(err => {
                console.log(err)
            })

    },[])
   
    //information on each object from the API
    const SingleData = (index) => {
        setvalue(index);
        setOpen(!open);
        
    //Scroll Animation
      index === '' ? animateScrollTo(document.querySelector('.close-info')) : animateScrollTo(document.querySelector('.detail-info'));    
    }

    //Add Google Maps
    function GetMap(props){
      
     const AnyReactComponent = ({ text }) => <div>{text}</div>;
     const defaultmarkers = {
        center: {
          lat: 40.263569,
          lng: -76.889832
        },
        zoom: 11
      } 
  
      return (
        <div style={{ height: '100vh', width: '100%' }}>
          <GoogleMapReact
            bootstrapURLKeys={{ key:"AIzaSyBu5nZKbeK-WHQ70oqOWo-_4VmwOwKP9YQ"}}
            defaultCenter={defaultmarkers.center}
            defaultZoom={defaultmarkers.zoom}
          >

          <AnyReactComponent
            lat={40.263569}
            lng={-76.889832}
            text="My Marker"
          />
          </GoogleMapReact>
        </div>
      );
    }
   
    return (
        <> 
        <div className={"close-info"}>
        <Container>
          <Row> <Col>      
             <ul>
             <CardDeck>   
                { posts.map(post => (
                    <li className="brew" key={post.id}>
                      <Card bg="light" text="black" style={{ width: '20rem'}} className="border border-info rounded-top-lg">
                        <Card.Body >
                        <Card.Title>
                          <Jumbotron className={"Jumbotron Jumbotron-fluid"}>
                            <h3 className="display-5">{post.name}</h3>
                          </Jumbotron>
                        </Card.Title><br/>
                        <Card.Subtitle><h4 className="display-5" style={{color:"gray"}}>{post.brewery_type}</h4></Card.Subtitle><br/>
                        <Card.Subtitle><h5> {post.street}{" "}
                                            {post.city}{" "}
                                            {post.state}{" "}
                                            {post.zip}
                                        </h5>
                        </Card.Subtitle>
                        <Card.Link href={post.website_url} title={post.name}>
                            <h6>{post.website_url}</h6>
                        </Card.Link><br/><br/>
                       <div className= "d-flex justify-content-end">
                            <Button variant="info" size="md" title={"Click Me!"} 
                                aria-controls="example-collapse-text"
                                aria-expanded={open}
                                onClick ={SingleData.bind(this,post)}>Scroll For Details
                            </Button> 
                       </div>

                      </Card.Body>
                      </Card>                    
                    </li>
                    ))}  
                    </CardDeck>                  
                </ul>
            </Col></Row>
            </Container>

<div className="detail-info">
            <Collapse in= {open}>
            <Container id="example-collapse-text">
               <Card bg="light" text="black" style={{ width: '20rem'}} className="border border-info rounded-top-lg">
                    <Card.Body> 
                    <div>
                    {values.name > '' && (
                       <div className= "d-flex justify-content-end">
                          <Button variant="warning" size="md" title ={"Close"} onClick={SingleData.bind(this,'')}>Close</Button>
                       </div>
                    )}<br/>
                    <Card.Title>
                      <Jumbotron className={"Jumbotron Jumbotron-fluid"}>
                        <h3 className="display-5">{values.name}</h3>
                      </Jumbotron>
                    </Card.Title><br/>
                    <Card.Subtitle>
                      <h5>
                          {values.street}
                          {values.city}{" "}
                          {values.state}{" "}
                          {values.zip}
                      </h5>
                    </Card.Subtitle>                      
                    </div>
                    </Card.Body>
                </Card>
            </Container>
            </Collapse></div>
            <br/><br/>

            <div><GetMap /> </div> 
        </div>
</>
    )
}
 export default DataFetching;

 
